/**
 *   Name: Bizagi Tablet Render fileprint Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the fileprint render class to adjust to tablet devices
 */

// Extends from render tablet base
bizagi.rendering.tablet.render.extend("bizagi.rendering.tablet.fileprint", bizagi.rendering.fileprint.prototype);
// Extends itself
bizagi.rendering.tablet.fileprint.extend("bizagi.rendering.tablet.fileprint", {}, {

  /**
                                                                                     * Method to customize each render after processed
                                                                                     */
  postRender: function postRender() {
    this.getControl().parent().hide();
  } });