/*
*   Name: BizAgi tablet exclusiveCheck Column Decorator Extension
*   Author: RicharU
*   Comments:
*   -   This script will redefine the check column to adjust to tablet devices
*/

// Extends from column
bizagi.rendering.tablet.columns.column.extend("bizagi.rendering.tablet.columns.exclusiveCheck", {}, {
  /*
                                                                                                       *   Returns the in-memory processed element
                                                                                                       *   so the caller could append it to any place
                                                                                                       */
  render: function render(surrogateKey, value) {

    var self = this;
    // Set flag to check when a render has changed
    self.changed = false;
    var properties = self.properties;
    var form = self.getFormContainer();
    var gridChanges = self.grid.changes;
    // Set grid and id references to the control in order to render the content
    var decorated = self.getDecorated(surrogateKey);
    decorated.grid = self.grid;
    decorated.column = this;
    decorated.surrogateKey = surrogateKey;

    // Set cell value                       
    if (self.getMode() == "execution") {
      self.setValue(surrogateKey, value || false);
      var tmpCheck = self.grid.enabledChecks[self.properties.xpath].find(function (el) {
        return el.id == surrogateKey;
      });
      if (typeof tmpCheck == "undefined") {
        self.grid.enabledChecks[self.properties.xpath].push({
          id: surrogateKey,
          value: value || false,
          originalValue: value || false,
          changed: false });

      }
    }


    if (value == null) {
      self.changed = true;
      var obj = {};
      obj[self.properties.xpath] = self.getValue(surrogateKey);
      if (bizagi.util.isEmpty(gridChanges[surrogateKey])) gridChanges[surrogateKey] = {};
      gridChanges[surrogateKey] = $.extend({}, gridChanges[surrogateKey], obj);
    }

    // Override render properties
    self.overrideDecoratedRenderProperties(surrogateKey);


    // If the render is not editable return the readonly version of the column
    if (!properties.editable)
    return self.renderReadOnly.apply(self, arguments);

    // Returns the decorated render inside a custom layout for columns
    self.readyDeferred = new $.Deferred();
    var cell = decorated.render("cell");

    // If the render changes set the flag on
    decorated.bind("renderchange", function () {
      self.changed = true;
      var checkList = self.grid.enabledChecks[self.properties.xpath];

      checkList.forEach(function (el) {
        if (el.id == surrogateKey) {
          el.value = decorated.value;
          el.changed = true;
        } else
        if (decorated.value && el.value) {
          var tmpDecorated = self.getDecorated(el.id);
          tmpDecorated.setDisplayValue(false);
          self.setValue(el.id, false);
          el.value = false;
          el.changed = true;
          if (form.isOfflineForm) {
            self.handleOfflineChange(tmpDecorated, el);
          }
        }
      });

      var simpleXpathArray = self.properties.xpath.split(".");
      self.grid.exclusiveChanges[self.properties.xpath] = {
        xpath: self.properties.xpath,
        simpleXpath: simpleXpathArray[simpleXpathArray.length - 1],
        value: self.getValue(surrogateKey),
        key: surrogateKey };

    });

    self.bind("rendered", function () {
      self.readyDeferred.resolve();
    });


    return cell;
  },

  /*
      *   Post process the element after it has been rendered
      */
  postRender: function postRender(surrogateKey, cell) {
    // Call base
    this._super(surrogateKey, cell);

    var self = this;
    var mode = self.getMode();

    this.setSurrogateKey(surrogateKey);
    if (mode == "execution") {

      var decorated = this.getDecorated(surrogateKey);
      var control = decorated.getControl();

      // Check if its new row
      if (control.parents("tr").data("new-row")) {
        //Set the column with this attribute, so when the control is evaluated in isValid(), validated
        //also if it is required, and belongs from a new row
        self.isNewRow = true;
      } else {
        self.isNewRow = false;
      }
    }
  },

  isValueValid: function isValueValid(surrogateKey, value, invalidElements, propertyOverrides) {
    var self = this;
    self.grid.enabledChecks = self.grid.enabledChecks || {};
    var checkedValues = (self.grid.enabledChecks[self.properties.xpath] || []).filter(function (el) {
      return el.value;
    });

    if (checkedValues.length > 1) {
      var message = self.getResource("render-exclusive-column-check-error").replaceAll("#label#", self.properties.displayName);
      /* var messageData = {xpath: self.properties.xpath, message: message}; */
      invalidElements.push(message);
      return false;
    } else {
      return true;
    }

  },
  /**
     * Add change in grid changes and update grid data rows
     * @param {*} decorated 
     * @param {*} element 
     */
  handleOfflineChange: function handleOfflineChange(decorated, element) {
    var self = this;
    var gridChanges = self.grid.changes;
    var newChange = {};
    newChange[decorated.properties.xpath] = element.value;
    if (bizagi.util.isEmpty(gridChanges[element.id])) gridChanges[element.id] = {};
    gridChanges[element.id] = $.extend({}, gridChanges[element.id], newChange);
    //Update grid data (grid.properties.data)
    self.grid.onCellChange(self, null, element.id);
  } });