/**
 *   Name: Bizagi Tablet Tab Container Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the container class to adjust to tablet devices
 */

// Extends from container tablet base
bizagi.rendering.tablet.container.extend("bizagi.rendering.tablet.tab", bizagi.rendering.tab.prototype);
// Auto extend
bizagi.rendering.tablet.tab.extend("bizagi.rendering.tablet.tab", {}, {

  /* POST RENDER CONTAINER ACTIONS
                                                                        =================================================*/
  postRenderContainer: function postRenderContainer(container) {
    var self = this;

    this._super(container);

    container.material_bizagi_tabs({
      direction: this.properties.orientation,
      active_index_tab: this.activeTab }).
    on("updateActive", function (ev, info) {
      ev.stopPropagation();

      self.setActiveTab(info.index);

    });

    $("> .bz-tabs > .bz-tab", container).rippleEffect();
  } });