/*  
 *   Name: Bizagi Tablet Range Date Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the range date render class to adjust to tablet devices
 */

// Extends from range tablet base
bizagi.rendering.tablet.range.extend("bizagi.rendering.tablet.rangeNumber", bizagi.rendering.rangeNumber.prototype);
// Extends itself
bizagi.rendering.tablet.rangeNumber.extend("bizagi.rendering.tablet.rangeNumber", {}, {

  /**
                                                                                         * Render a specific implementation for Desktop device
                                                                                         */
  postRender: function postRender() {
    var self = this;

    var minControl = new bizagi.rendering.number(self.getRenderProperties("min"));
    var maxControl = new bizagi.rendering.number(self.getRenderProperties("max"));

    self.setRanageControls(minControl, maxControl);
  },

  getRenderProperties: function getRenderProperties(typeRangeControl) {
    var self = this;
    var form = self.getFormContainer();
    var properties = self.properties;

    var renderProperties = {
      data: {
        properties: {
          "id": properties.id,
          "xpath": properties.xpath } },


      mode: "execute",
      parent: form,
      renderFactory: self.renderFactory,
      resources: bizagi.localization,
      dataService: self.dataService };


    if (typeRangeControl === "min") {
      renderProperties.data.properties.displayName = "From";
    } else {
      renderProperties.data.properties.displayName = "To";
    }

    return renderProperties;
  },

  /**
      * Render actual value of control and set events
      * @param data
      */
  setDisplayValue: function setDisplayValue(data) {
    var self = this;
    var control = self.getControl();
  } });