// Extends from render tablet base
bizagi.rendering.tablet.render.extend("bizagi.rendering.tablet.label", bizagi.rendering.label.prototype);
// Extends itself
bizagi.rendering.tablet.label.extend("bizagi.rendering.tablet.label", {}, {
  /**
                                                                            * Method to render non editable values
                                                                            */
  renderReadOnly: function renderReadOnly() {
    this.setValue(this.properties.displayName);
    return this._super();
  },
  /**
      * Sets the value in the rendered control
      */
  setDisplayValue: function setDisplayValue(value) {
    // DON'T DO ANYTHING
  } });