// Extends itself
$.Class.extend("bizagi.rendering.tablet.slide.searchForm", {}, {

  /**
                                                                  * Constructor
                                                                  */
  init: function init(dataService, renderFactory, searchForms, searchParams, searchFormParams) {
    var self = this;

    self.dataService = dataService;
    self.renderFactory = renderFactory;
    self.searchForms = searchForms;
    self.searchParams = searchParams;
    self.searchFormParams = searchFormParams;

    self.searchFormDeferred = new $.Deferred();

    // Creates a container, be careful you must destroy it later
    self.container = self.searchFormParams.navigation.createRenderContainer({ title: self.searchFormParams.title });
  },

  /**
      * Render the form
      * The params are the same that will be send to the ajax service
      * @param {} params
      * @returns {} Returns a deferred
      */
  render: function render(params) {
    this.properties = params;

    // Render the search form
    this.renderSearchForm(params);

    // Return promise
    return this.searchFormDeferred.promise();
  },

  /**
      * Render the search form
      * @param {} params
      * @returns {}
      */
  renderSearchForm: function renderSearchForm(params) {
    var self = this;

    bizagi.loading.start();

    // Creates a dialog
    $.when(self.getSearchContainerData(params)).then(function (data) {
      if (data.form.properties) {
        data.form.properties.orientation = self.searchParams.orientation || "ltr";
      }

      // Render dialog form
      var form = self.renderFactory.getContainer({
        type: "form",
        data: data.form,
        navigation: self.searchFormParams.navigation,
        view_container: self.container });


      form.buttons = self.getSearchButtons();

      // Save form reference
      self.form = form;

      (Object.keys(form.children) || []).
      map(function (key) {
        form.children[key].params = $.extend({}, form.children[key].params, self.searchFormParams);
      });

      // Return rendering promise
      return form.render();
    }).done(function (element) {
      var firstChild = self.form.firstChild();

      // Append form in the slide view
      self.container.element.html(element);
      self.searchFormParams.navigation.navigate(self.container.id);

      // Show action buttons
      //self.container.setNavigationButtons(self.form);

      // Publish an event to check if the form has been set in the DOM
      self.form.triggerHandler("ondomincluded");

      // Set currents search form
      self.currentSearchForm = firstChild;
      self.currentSearchForm.triggerHandler("ondomincluded");

      self.currentSearchForm.bind("instancerefresh", function () {
        // Show search results container
        self.searchResultContainer.show();
      });

      self.form.bind("refresh", function (_, refreshParams) {
        refreshParams.scrollTop = self.form.container.parent().scrollTop();
        refreshParams = $.extend({
          focus: focus,
          selectedTabs: self.form.getSelectedTabs(),
          isRefresh: true,
          contextType: self.properties.contextType || "",
          navigation: self.searchFormParams.navigation,
          view_container: self.container },
        refreshParams);

        return self.renderSearchForm(refreshParams);
      });

      // Add row selected handlers to resolve the deferred
      self.currentSearchForm.bind("instanceSelected", function (ev, ui) {
        // Close slide
        self.goBack();

        self.searchFormDeferred.resolve({ id: ui.id, label: "" });
      });

      // if is any modal view displayed, hide it to allow this search form been displayed
      // self.handleSearchFormZindex("show");

      // Hide content
      self.containerSearchCriteria = self.currentSearchForm.container.find(".bz-container__search-criteria");
      self.searchResultContainer = $(".bz-container__search-results", element);
      self.searchResultContainer.hide();

    }).fail(function (error_obj) {
      if (self.hasSessionFormException(error_obj)) {
        self.goToStartPage(error_obj);
      } else {
        var errorTemplate = self.renderFactory.getCommonTemplate("form-error");
        var message = error_obj && error_obj.message ? error_obj.message : error_obj;

        $($.fasttmpl(errorTemplate, { message: message })).
        appendTo(self.container.element);

        self.searchFormParams.navigation.navigate(self.container.id);
      }
    }).always(function () {
      bizagi.loading.stop();
    });
  },

  /**
      * Get the addform and show it
      * @param {} params
      * @returns {}
      */
  renderAddForm: function renderAddForm(params) {
    var self = this;
    params = params || {};
    var properties = self.properties;

    if (typeof self.addContainer === "undefined") {
      self.addContainer =
      self.searchFormParams.navigation.createRenderContainer({
        title: bizagi.localization.getResource("render-dialog-entity") });


      // Binds the view close event, and send the RollBack
      self.addContainer.element.bind("close", function () {
        delete self.addContainer;
      });
    }

    var isRefresh = params.isRefresh || false;

    var getFormParams = {
      idPageCache: params.idPageCache,
      guidEntity: properties.entity,
      contextType: "entity" };


    if (isRefresh) {
      getFormParams.isRefresh = true;
      getFormParams.idStartScope = params.idStartScope;
    } else {
      getFormParams.idRender = properties.idRender;
      getFormParams.requestedForm = "addForm";
    }

    bizagi.loading.start();

    //Get the addform
    $.when(self.dataService.getFormData(getFormParams)).then(function (data) {
      if (params.editable === false) {
        data.form.properties.editable = false;
      }

      if (data.form.properties) {
        data.form.properties.orientation = self.searchParams.orientation || "ltr";
      }

      data.form.contextType = "entity";

      var form = self.renderFactory.getContainer($.extend({}, self.addFormParams, {
        type: "form",
        data: data.form,
        focus: params.focus || false,
        selectedTabs: params.selectedTabs,
        isRefresh: isRefresh,
        requestedForm: params.requestedForm,
        navigation: self.searchFormParams.navigation,
        view_container: self.addContainer }));



      form.buttons = self.getAddFormButtons(form);

      $.extend(form, {
        processButtons: function processButtons() {
          //not send information set in memory and save

          $.each(form.buttons, function (index, element) {
            switch (element["ordinal"]) {
              case 1:
                // Next button will behave as a cancel button
                $(form.getButtons()[index]).click(function () {
                  self.addContainer.destroy();
                  delete self.addContainer;
                });

                form.getButtons()[index].innerHTML = bizagi.localization.getResource("render-form-dialog-box-close");
                form.buttons[index].caption = bizagi.localization.getResource("render-form-dialog-box-close");
                form.buttons[index].action = "back";

                break;

              default:
                $(form.getButtons()[index]).click(function () {
                  self.saveDataAddForm(form);

                  self.addContainer.destroy();
                  delete self.addContainer;
                });
                break;}

          });
        } });


      (Object.keys(form.children) || []).
      map(function (indicator) {
        form.children[indicator].params = $.extend({}, form.children[indicator].params, self.addFormParams);
      });

      return $.when(form.render()).done(function (element) {
        // Set currents search form
        self.currentAddForm = form.firstChild();
        self.addContainer.element.html(element);

        if (!isRefresh) {
          self.searchFormParams.navigation.navigate(self.addContainer.id, "overlay:up");
        }

        self.addContainer.setNavigationButtons(form);

        form.triggerHandler("ondomincluded");

        // if is any modal view displayed, hide it to allow this search form been displayed
        self.handleSearchFormZindex("show");

        form.bind("refresh", function (_, refreshParams) {
          refreshParams.scrollTop = self.form.container.parent().scrollTop();
          refreshParams = $.extend({
            focus: focus,
            selectedTabs: self.form.getSelectedTabs(),
            isRefresh: true,
            contextType: self.properties.contextType || "",
            navigation: self.searchFormParams.navigation,
            view_container: self.addContainer },
          refreshParams);

          return self.renderAddForm(refreshParams);
        });
      });
    }).fail(function (error) {
      var error_obj = bizagi.util.getErrorFormatted(error);

      if (self.hasSessionFormException(error_obj)) {
        self.goToStartPage(error_obj);
      } else {
        self.addContainer.destroy();
        delete self.addContainer;
      }
    }).always(function () {
      bizagi.loading.stop();
    });
  },

  /**
      * Save data to add form
      * @param {} form
      * @returns {}
      */
  saveDataAddForm: function saveDataAddForm(form) {
    var self = this;
    var defer = $.Deferred();

    if (form.validateForm()) {
      var data = {};
      $.forceCollectData = true;

      form.collectRenderValues(data);
      data.idPageCache = form.idPageCache;

      var params = {
        action: "SAVE",
        data: data,
        idPageCache: data.idPageCache,
        guidEntity: form.properties.entity,
        contexttype: "entity" };


      // Check if the add form has data
      var formData = {};

      form.collectRenderValuesForSubmit(formData);

      // Turn off flag
      $.forceCollectData = false;

      if (!bizagi.util.isMapEmpty(formData)) {

        bizagi.loading.start();

        $.when(self.dataService.submitData(params)).done(function (response) {
          function dataHasXpath(xpath) {
            var render = self.currentSearchForm.getRender(xpath);
            if (_typeof(render) === "object" && render !== null)
            return { "found": true, "obj": render };
            return { "found": false, "obj": {} };
          }

          if (response && response.type === "validationMessages") {
            form.addValidationMessage(response.messages);
            defer.reject();
          } else {

            for (var i in formData) {
              var mapping = dataHasXpath(i);
              if (mapping.found) {
                mapping.obj.setValue(formData[i]);
                self.setDisplayValueToControl(mapping.obj, form, i, formData);
              }
            }

            defer.resolve();
          }
        }).fail(function (error) {
          var error_obj = bizagi.util.getErrorFormatted(error);

          if (self.hasSessionFormException(error_obj)) {
            self.goToStartPage(error_obj);
          } else {
            var message = error_obj.message || bizagi.localization.getResource("workportal-general-error");

            form.addValidationMessage(message);
          }

          defer.reject();
        }).always(function () {
          bizagi.loading.stop();
        });
      } else {
        // Form has not data
        bizagi.showMessageBox(bizagi.localization.getResource("render-search-advanced-no-filters"));
        defer.reject();
      }
    } else {
      defer.reject();
    }

    return defer.promise();
  },

  /***
      * Set display value by control type, when add new record
      * @param renderControlSearchForm
      * @param addForm
      * @param xpath
      * @param formData
      */
  setDisplayValueToControl: function setDisplayValueToControl(renderControlSearchForm, addForm, xpath, formData) {
    if (renderControlSearchForm.properties.type === "searchSuggest") {
      renderControlSearchForm.setDisplayValue(addForm.getRender(xpath).getDisplayValue());
    } else {
      renderControlSearchForm.setDisplayValue(formData[xpath]);
    }
  },

  /**
      * Get navigation buttons
      */
  getSearchButtons: function getSearchButtons() {
    var self = this;

    // Set initial actions
    var searchFormOptions = [{
      ordinal: 1,
      action: "search",
      caption: bizagi.localization.getResource("render-form-dialog-box-search"),
      callback: function callback() {
        self.currentSearchForm.performSearch(self.searchParams);
      } },
    {
      ordinal: 2,
      action: "cancel",
      caption: "cancel",
      callback: function callback() {
        // Close slide
        self.goBack();
      } }];


    if (self.searchFormParams.allowNew) {
      searchFormOptions.push({
        ordinal: 3,
        action: "add",
        caption: bizagi.localization.getResource("render-form-dialog-box-add"),
        callback: function callback() {
          var params = {
            idCase: self.searchFormParams.idCase,
            idRender: self.properties.idRender,
            displayName: self.properties.displayName,
            idPageCache: self.properties.idPageCache };


          self.renderAddForm(params);
        } });

    }

    return searchFormOptions;

  },

  /**
      * Creates the search dialog sub-containers
      * @param {} params
      * @returns {}
      */
  getSearchContainerData: function getSearchContainerData(params) {
    var self = this;
    var deferred = new $.Deferred();
    var data = {
      form: {
        properties: {},
        elements: [] } };



    var container = data.form;
    var searchFormToLoad = self.searchForms[0];

    // When there are more than one search form, render a tab container
    if (self.searchForms.length > 1) {

      var tabContainer = {
        properties: {
          type: "tab" },

        elements: [] };


      var otherElements = [];

      $.each(self.searchForms, function (i, searchForm) {
        var isDefault = searchForm["default"] || false;

        // Create tab item
        var tab = {
          properties: {
            id: i,
            type: "tabItem",
            displayName: searchForm.caption },

          elements: [] };


        // Append children
        if (isDefault) {

          tabContainer.elements.push({ container: tab });

          // Set properties to load default container
          container = tab;
          searchFormToLoad = self.searchForms[0];

        } else {
          otherElements.push({ container: tab });
        }
      });

      // Append no defaults elements
      $.each(otherElements, function (i, element) {
        tabContainer.elements.push(element);
      });

      data.form.elements.push({
        container: tabContainer });

    }

    bizagi.loading.start();

    // Loads default form
    $.when(self.getSearchFormData(params, searchFormToLoad)).
    done(function (searchFormData) {
      container.elements.push(searchFormData);
      container.properties.id = Math.guid();

      // Resolve deferred
      deferred.resolve(data);
    }).fail(function (error) {
      var message = bizagi.util.getErrorFormatted(error);
      deferred.reject(message);
    }).always(function () {
      bizagi.loading.stop();
    });

    return deferred.promise();
  },

  /**
      * Loads a search form
      * @param {} params
      * @param {} searchForm
      * @returns {}
      */
  getSearchFormData: function getSearchFormData(params, searchForm) {
    var self = this;

    return self.dataService.getSearchFormData($.extend(params, {
      idSearchForm: searchForm.id,
      url: searchForm.url })).
    pipe(function (data) {
      // Append search render properties
      data.form.properties.idRender = params.idRender;

      return data;
    });
  },

  /**
      * Go back and destroy
      * @returns {}
      */
  goBack: function goBack() {
    this.handleSearchFormZindex("hide");
    this.container.destroy();
  },

  /**
      * Show modal view
      * @param option
      */
  handleSearchFormZindex: function handleSearchFormZindex(option) {
    // If there is an open modal, change z-index to make searchForm visible
    var modalViewDisplayed = $.grep($(".km-modalview-root"), function (n) {
      return $(n).css("display") === "block";
    });

    if (modalViewDisplayed.length > 0) {
      var index = option === "show" ? 0 : 10001;
      $(modalViewDisplayed).css("z-index", index);
    }
  },

  /**
      * Idle timer expired exception
      * @param error
      * @returns {boolean}
      */
  hasSessionFormException: function hasSessionFormException(error) {
    return typeof error !== "undefined" && error.code === "AUTH_ERROR";
  },

  /**
      * Navigate to main page
      * @param error
      */
  goToStartPage: function goToStartPage(error) {
    if (this.searchFormParams.navigation) {
      this.searchFormParams.navigation.goToStartPage(error);
    }
  },

  /**
      * Get buttons for Add form
      * @param form
      * @returns {[*, *]}
      */
  getAddFormButtons: function getAddFormButtons(form) {
    var self = this;

    return [
    $.extend(form.buttons[0] || {}, {
      "caption": bizagi.localization.getResource("render-form-dialog-box-save"),
      "actions": ["submitData", "refresh"],
      "submitData": true,
      "refresh": true,
      "ordinal": 0,
      "action": "save",
      "save": true,
      "style": "" },
    {
      //This callback avoid to call the default processButton in the form
      callback: function callback() {
        // When the data is saved then
        self.saveDataAddForm(form).done(function () {

          self.addContainer.destroy();
          delete self.addContainer;

          // TIMER: Prevent the Form is locked if not destroy the addForm
          setTimeout(function () {
            // Force a search after a new item is created!
            self.currentSearchForm.performSearch(self.searchParams);
          }, 350);
        });
      } }),

    $.extend(form.buttons[1] || {}, {
      "caption": bizagi.localization.getResource("render-form-dialog-box-close"),
      "actions": ["submitData", "refresh"],
      "submitData": true,
      "refresh": true,
      "ordinal": 1,
      "action": "back",
      "save": true,
      "style": "" },
    {
      // This callback avoid to call the default processButton in the form
      callback: function callback() {
        self.addContainer.destroy();
        delete self.addContainer;
      } })];


  } });