/**
 *   Name: Bizagi Render CollectionNavigator
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for collection navigator renders
 */

// Extends from render tablet base
bizagi.rendering.tablet.render.extend("bizagi.rendering.tablet.collectionnavigator", bizagi.rendering.collectionnavigator.prototype);
// Extends itself
bizagi.rendering.tablet.collectionnavigator.extend("bizagi.rendering.tablet.collectionnavigator", {}, {

  /**
                                                                                                         * Method to customize each render after processed
                                                                                                         */
  postRender: function postRender() {
    this.getControlFiller().addClass("bz-form-item__control--hidden");
  },

  /**
      * Method to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    this.getControlFiller().addClass("bz-form-item__control--hidden");
  } });